import { FunctionComponent } from "react";
import { Property } from "csstype";
import { css } from "@emotion/css";

type ComponentType = {
  prop?: string;

  /** Style props */
  propAccentColor?: Property.AccentColor;
};

const Component: FunctionComponent<ComponentType> = ({
  prop,
  propAccentColor,
}) => {
  return (
    <div
      className={css`
        position: relative;
        font-size: 32px;
        line-height: 34px;
        font-family: "Baloo Bhai";
        color: #1262af;
        text-align: right;
        accent-color: ${propAccentColor};
      `}
    >
      {prop}
    </div>
  );
};

export default Component;
